<template>
<div>
  <div>

  <h1>full name : {{ fullName }}</h1>
  <h1>full name : {{ getFullName() }}</h1>
  </div>
 </div>
</template>

<script>
export default {
  data() {
    return {
      firstName: "길동",
      lastName: "홍",
    };
  },
  methods: {
    getFullName() {
      console.log("메써드 함수 호출");
      return this.firstName + "" + this.lastName;
    },
  },
  computed: {
    fullName() {
      //fullName:길동홈
      console.log("컴퓨티드 함수 호출");
      return this.firstName + "" + this.lastName;
    },
  },
};
</script>
<style scoped></style>